<template>
	<div class="table-container">
		<div id="table"></div>
		<div id="map"></div>
		<el-row>
			<el-col :span="12" id="lol-pos">
				
			</el-col>
			<el-col :span="12" id="lol-mvp">
				
			</el-col>
		</el-row>
	</div>
</template>

<script>
	import Highcharts from 'highcharts';
	import HighchartsDrilldown from 'highcharts/modules/drilldown';
	import HighchartsMore from 'highcharts/highcharts-more';
	HighchartsMore(Highcharts);
	HighchartsDrilldown(Highcharts);
	import Vue from 'vue';
	import {Row,Col} from 'element-ui';
	Vue.component(Row.name,Row);
	Vue.component(Col.name,Col);
	export default {
		beforeRouteEnter(to,from,next){
			
			next(vm => {
				
				
				/**
				 * @desc 框架熟练度
				 * 
				 * 热门框架
				 */
				Highcharts.chart('table', {
			        chart: {
			            type: 'column'
			        },
			        credits : {
						enabled : false
					},
			        title: {
			            text: '框架熟悉度自我评价'
			        },
			        subtitle: {
			            text: 'me的技能熟悉度图表'
			        },
			        xAxis: {
			            type: 'category'
			        },
			        yAxis: {
			            title: {
			                text: '基本熟悉度'
			            }
			        },
			        legend: {
			            enabled: false
			        },
			        plotOptions: {
			            series: {
			                borderWidth: 0,
			                dataLabels: {
			                    enabled: true,
			                    format: '{point.y:.1f}%'
			                }
			            }
			        },
			        tooltip: {
			            headerFormat: '<span style="font-size:11px">{series.name}</span><br>',
			            pointFormat: '<span style="color:{point.color}">{point.name}</span>: <b>{point.y:.2f}%</b> of total<br/>'
			        },
			        series: [{
			            name: '框架熟练度',
			            colorByPoint: true,
			            data: [{
			                name: 'JS',
			                y: 90,
			                drilldown: 'JS',
			                color : '#66b3ff'
			            }, {
			                name: 'Ng',
			                y: 50,
			                drilldown: 'Ng',
			                color : '#e62a39'
			            }, {
			                name: 'Vue',
			                y: 70,
			                drilldown: 'Vue',
			                color : '#4fc08d'
			            }, {
			                name: 'Mui',
			                y: 80,
			                drilldown: 'Mui',
			                color : '#d74b28'
			            }, {
			                name: 'Jquery',
			                y: 90,
			                drilldown: 'Jquery',
			                color : '#78cff5'
			            }, {
			                name: 'React',
			                y: 8,
			                drilldown: null,
			                color :'#61dafb'
			            },{
			                name: 'Bootstrap',
			                y: 90,
			                drilldown: 'Bootstrap',
			                color : '#8854d6'
			            }
			            
			            ]
			        }],
			        drilldown: {
			            series: [{
			                name: 'JS',
			                id: 'JS',
			                data: [
			                    [
			                        'ECMA5',
			                        90
			                    ],
			                    [
			                        'ECMA6',
			                        80
			                    ],
			                    [
			                        'ECMA7',
			                        10
			                    ]
			                ]
			            }, {
			                name: 'Ng',
			                id: 'Ng',
			                data: [
			                    [
			                        'v1.x',
			                        40
			                    ],
			                    [
			                        'v2.x',
			                        5
			                    ],
			                    [
			                        'v4.x',
			                        50
			                    ]
			                ]
			            }, {
			                name: 'Vue',
			                id: 'Vue',
			                data: [
			                    [
			                        'v1.x',
			                        40
			                    ],
			                    [
			                        'v2.x',
			                        70
			                    ]
			                ]
			            }, {
			                name: 'Mui',
			                id: 'Mui',
			                data: [
			                    [
			                        'v1.x',
			                        0
			                    ],
			                    [
			                        'v2.x',
			                        80
			                    ],
			                    [
			                        'v3.x',
			                        80
			                    ]
			                ]
			            }, {
			                name: 'Jquery',
			                id: 'Jquery',
			                data: [
			                    [
			                        'v1.6+',
			                        90
			                    ],
			                    [
			                        'v.2.x',
			                        90
			                    ],
			                    [
			                        'v3.x',
			                        80
			                    ]
			                ]
			            },{
			                name: 'Bootstrap',
			                id: 'Bootstrap',
			                data: [
			                    [
			                        'v1.x',
			                        60
			                    ],
			                    [
			                        'v.2.x',
			                        72
			                    ],
			                    [
			                        'v3.x',
			                        90
			                    ],
			                    [
			                        'v4.x',
			                        80
			                    ]
			                ]
			            }]
			        }
			    });
				
				setTimeout(function  () {
					Highcharts.chart('map',{
						type : 'pie',
						credits : {
							enabled : false
						},
						title :{
							text : '近年来(框架/插件)使用量'
						},
						tooltip : {
							pointFormat: '{series.name}: <b>{point.percentage:.1f}%</b>'
						},
						 plotOptions: {
				            pie: {
				                allowPointSelect: true,
				                cursor: 'pointer',
				                depth: 35,
				                dataLabels: {
				                    enabled: true,
				                    format: '{point.name}'
				                }
				            }
				        },
				        series : [
				        	{
				        		type : 'pie',
				        		name : '框架使用情况',
				        		data : [
				        			['jq',10],
				        			['vue',10],
				        			['jekyll',10],
				        			['ng',5],
				        			['mui',15],
				        			['layer',10],
				        			['Hchart',10],
				        			['eleUi',10],
				        			['mintUi',10],
				        			['bstp',10],
				        			['sui',8],
				        			['date97',8],
				        			['H5+',12],
				        			['webpack',9],
				        			['grunt',9],
				        			['Git',14],
				        			['less',10],
				        			['C3',12]
				        		]
				        	}
				        ]
					});
				},300);
				
				setTimeout(function  () {
					Highcharts.chart('lol-pos',{
						chart : {
							type : 'line',
							polar : true
						},
						credits : {
							enabled : false
						},
						title : {
							text : 'LOL擅长位置'
						},
						 pane: {
				            size: '80%'
				        },
						legend: {
				            align: 'right',
				            verticalAlign: 'top',
				            y: 70,
				            layout: 'vertical',
				            
				            enabled : false
				        },
						xAxis: {
				            categories: ['上单', '中单',  '射手',
				                         '辅助', '打野'],
				            tickmarkPlacement: 'on',
				            lineWidth: 0
				        },
				        yAxis: {
				            gridLineInterpolation: 'polygon',
				            lineWidth: 0,
				            min: 0
				        },
				        series : [
				        	{
				        		data : [90,70,77,20,86],
				        		pointPlacement : 'on',
				        		color : '#F69'
				        	}
				        ]
					});
					Highcharts.chart('lol-mvp',{
						chart : {
							type : 'line',
							polar : true
						},
						credits : {
							enabled : false
						},
						title : {
							text : 'LOL统计'
						},
						 pane: {
				            size: '80%'
				        },
						legend: {
				            align: 'right',
				            verticalAlign: 'top',
				            y: 70,
				            layout: 'vertical',
				             enabled : false
				        },
						xAxis: {
				            categories: ['控制', '输出',  '生存',
				                         '助攻', '击杀'],
				            tickmarkPlacement: 'on',
				            lineWidth: 0
				        },
				        yAxis: {
				            gridLineInterpolation: 'polygon',
				            lineWidth: 0,
				            min: 0
				        },
				        series : [
				        	{
				        		data : [5000,12000,10000,9000,6666],
				        		pointPlacement : 'on',
				        		color : 'green'
				        	}
				        ]
					});
				},600);
				
			});
		}
	}
</script>

<style scoped>
	.table-container{
		padding: 30px;
		box-sizing: border-box;
	}
	#table{
		height: 300px;
		margin-bottom:30px;
	}
	#map{
		height: 400px;
		margin-bottom:30px;
	},
	#lol-pos,#lol-mvp{
		height: 300px;
	}
</style>


