<template>
  <div id="app"  v-loading.body="loading"  element-loading-text="哥们想要赚钱,搬到市中心">
  	<el-menu theme="dark" mode="horizontal" :router="true">
  		<el-menu-item index="/">Resume</el-menu-item>
  		<el-menu-item index="/blog">BLOG</el-menu-item>
  	</el-menu>
  	<el-row id="center">
  		<el-col :span="4" style="overflow-x: hidden;">
  			<el-menu  class="el-menu-vertical-demo"  :router="true">
  				<el-menu-item index="/"><i class="el-icon-information"></i>个人隐私</el-menu-item>
		      <el-submenu index="2">
		        <template slot="title"><i class="el-icon-setting"></i>技能相关</template>
		        <el-menu-item-group>
		          <el-menu-item index="/mastery">熟练度</el-menu-item>
		          <el-menu-item index="2-2">特效/插件</el-menu-item>
		        </el-menu-item-group>
		      </el-submenu>
  			</el-menu>
  		</el-col>
  		<el-col :span="20" id="content">
  			 <router-view></router-view>
  		</el-col>
  	</el-row>
  </div>
</template>

<script>
import Vue from 'vue'
import { Row,Col,Menu,MenuItem,Submenu,MenuItemGroup,Loading } from 'element-ui'

Vue.component(Row.name,Row);
Vue.component(Col.name,Col);
Vue.component(Menu.name,Menu);
Vue.component(MenuItem.name,MenuItem);
Vue.component(Submenu.name,Submenu);
Vue.component(MenuItemGroup.name,MenuItemGroup);
Vue.use(Loading);
export default {
  name: 'app',
  data(){
  	return {
  		loading: true
  	}
  },
  created (){
  	/*this.loading = false;*/
  	setTimeout(() => this.loading = false,1000);
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  height: 100%;
}
#center{
	height:calc(100% - 60px);
	overflow: hidden;
}
#center .el-col{
	height: 100%;
}
#content{
	overflow :auto;
}
#center .el-menu{
	height: 100%;
}
</style>



