
/**
 * @param {Object} options
 * 
 * count = [number] 从那个位置开始
 * duration = [time] 间隔时间
 * 
 * @constructor
 * 
 * @description 打字插件的构造函数
 * 
 * @example let writed = new timer({});
 * 
 */
function timer (options) {
			
			
	/**
	 * @property {Object} options 参数设置
	 */
	this._setting = options || {};
	
	this._setting.count = options.count || 0;
	
	this._setting.duration = options.duration || 60;
	
	
	if ( typeof this._setting.stopNum != 'number' ){
		throw new Error('need stop timer number');
	} else {
		if ( this._setting.stopNum < 1 ){
			throw new Error('stopNum 不能小于 1');
		}
	}
}

/**
 * timer.prototype
 */
timer.prototype = {
	stop : function  () {
		clearInterval(this.timer);
	},
	play : function  (params) {
		
		let _this = this;
		
		let stopNum = params.stop || this._setting.stopNum;
		
		if ( stopNum <= this._setting.count ){
			throw new Error('old calc number');
		}
		if ( typeof params.play !== 'function' ){
			throw new Error('not find implement function name is play');
		}
		
		this.timer = setInterval(function  () {
			
			params.play(_this._setting.count);
			
			_this._setting.count ++;
			
			if ( _this._setting.count == stopNum ){
				_this.stop();
				_this._setting.count = 0;
				params.callback && params.callback.call(_this);
			}
			
		},this._setting.duration);
	}
}

export {timer};



