import Vue from 'vue'
import Router from 'vue-router'
import HomePage from '@/pages/Home'
import MasteryPage from '@/pages/Mastery'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'default',
      component: HomePage
    },
    {
      path: '/mastery',
      name: 'mastery',
      component: MasteryPage
    },
    {
      path: '/blog',
      redirect : to => {
      	window.location.href = 'https://chenyall.github.io'
      }
    }
  ]
})



