<template>
	<el-row id="home-container">
		<el-col :span="12" id="wirte-style">
			
			<el-collapse v-model="activeName">
				<el-collapse-item name="html" title="&lt;html&gt;">
					<pre>
						<code  class="html" v-text="rBody">
							
						</code>
					</pre>
				</el-collapse-item>
				<el-collapse-item name="css" title="&lt;css&gt;">
					<pre>
						<code class="css" id="mystyle">
							
						</code>
					</pre>
				</el-collapse-item>
			</el-collapse>
			
			
		</el-col>
		
		<el-col :span="12" id="write-view" v-html="rBody"></el-col>
	</el-row>
</template>

<script>
	import Vue from 'vue';
	import {timer} from '../public/Timer.js';
	import {data} from '../public/write.js'
	import {Row,Col,Collapse,CollapseItem} from 'element-ui';
	import hljs from 'highlight.js';
	hljs.initHighlightingOnLoad();
	Vue.component(Row.name,Row);
	Vue.component(Col.name,Col);
	Vue.component(Collapse.name,Collapse);
	Vue.component(CollapseItem.name,CollapseItem);
	export default {
		beforeRouteEnter(to,from,next){
			
			next(vm => {
				
				setTimeout(()=>{
					
					let VStyles = document.getElementById('mystyle');
					let styleContainer = document.getElementById('mainStyle');
					
					let myCode = document.querySelectorAll('pre code');
					function render () {
						
						myCode.forEach(function  (v,k) {
							hljs.highlightBlock(v);
						})
						
					}
					
					let wView = new timer({
						count : 0,
						duration : 80,
						stopNum : data.view1.length
					});
					
					let wStyle = new timer({
						count : 0,
						duration : 80,
						stopNum : data.style1.length
					});
					
					let calcNum = 1;
					
					function  start(iNum,iLen) {
						wView.play({
							stop : data['view'+iNum].length,
							play (n){
								vm.rBody += data['view'+iNum][n];
								
							},
							callback (){
								render();
								wStyle.play({
									stop:data['style'+iNum].length,
									play (n){
										vm.lBody += data['style' + iNum][n];
										VStyles.innerHTML = vm.lBody;
										styleContainer.innerHTML = vm.lBody;
										render();
									},
									callback(){
										iNum ++;
										if ( iNum <= iLen ){
											start(iNum,iLen);
										}
									}
								});
							}
						});
					}
					
					start(calcNum,data.length);
				},500);
				
			});
		},
		data (){
			return {
				lBody : '',
				rBody : '',
				activeName : ['css','html']
			}
		}
	}
</script>

<style scoped>
	.caifan{
		box-sizing: border-box;
		padding: 20px;
		text-align: center;
	}
	#home-container > .el-col{
		padding: 20px;
		
		
	}
</style>


